"use strict";

// TODO: Tests
// TODO: JSDoc
// TODO: Debugs

class ArgHandler {
    constructor(name) {
        this._name = name;
        this._cases = [];
    }

    case(types, handler) {
        if (arguments.length === 1) {
            handler = types;
            types = [];
        }

        if (!Array.isArray(types)) types = [types];
        this._cases.push({ types, handler });
        return this;
    }

    // TODO: Do we need to support allowed values (e.g. alignment can only be 'top', 'center', and 'bottom')?
    // TODO: Do we need a function callback support?
    _argsMatchTypes(args, types) {
        if (args.length !== types.length) return false;
        for (let i = 0; i < types.length; i++) {
            const type = types[i];
            const arg = args[i];
            if (type === '*') continue;
            if (type === 'string' && typeof arg !== "string") return false;
            if (type === 'boolean' && typeof arg !== "boolean") return false;
            if (type === 'number' && typeof arg !== "number") return false;
            if (type === 'integer' && typeof arg !== "number" && !Number.isInteger(arg)) return false;
            if (type === 'function' && typeof arg !== "function") return false;
            if (type === 'array' && !Array.isArray(arg)) return false;
            if (type === 'data' && (!arg || arg.constructor !== Date)) return false;
            if (type === 'object' && (!arg || arg.constructor !== Object)) return false;
            throw new Error(`Unkonwn type: "${type}`);
        }

        return true;
    }

    parse(args) {
        for (let i = 0; i < this._cases.length; i++) {
            const c = this._cases[i];
            if (this._argsMatchTypes(args, c.types)) {
                return c.handler.apply(null, args);
            }
        }

        throw new Error(`${this._name}: Invalid arguments.`);
    }
}

module.exports = ArgHandler;
